<template>
  <div class="prodetail">
    <van-nav-bar fixed title="商品详情页" left-arrow @click-left="$router.go(-1)" />

    <van-swipe :autoplay="3000" @change="onChange">
      <van-swipe-item v-for="item in  images" :key="item.file_id" >
        <img :src="item.external_url" />
      </van-swipe-item>

      <template #indicator>
        <div class="custom-indicator">{{ current + 1 }} / {{ images.length }}</div>
      </template>
    </van-swipe>

    <!-- 商品说明 -->
    <div class="info">
      <div class="title">
        <div class="price">
          <span class="now">￥{{ shopDatas.goods_price_min }}</span>
          <span class="oldprice">￥{{ shopDatas.goods_price_max }}</span>
        </div>
        <div class="sellcount">已售 {{ shopDatas.goods_sales }} 件</div>
      </div>
      <div class="msg text-ellipsis-2">
        {{ shopDatas.goods_name }}
      </div>

      <div class="service">
        <div class="left-words">
          <span><van-icon name="passed" />七天无理由退货</span>
          <span><van-icon name="passed" />48小时发货</span>
        </div>
        <div class="right-icon">
          <van-icon name="arrow" />
        </div>
      </div>
    </div>

    <!-- 商品评价 -->
    <div class="comment">
      <div class="comment-title">
        <div class="left">商品评价 ({{ commentsTotal }}条)</div>
        <div class="right">查看更多 <van-icon name="arrow" /> </div>
      </div>
      <div class="comment-list">
        <div class="comment-item" v-for="item in commentsList" :key="item.comment_id">
          <div class="top">
            <img :src="item.user.avatar_url || defaultImg" alt="">
            <div class="name">{{item.user.nick_name}}</div>
            <van-rate :size="16" :value="item.score / 4" color="#ffd21e" void-icon="star" void-color="#eee" />
          </div>
          <div class="content">
             {{item.content}}
          </div>
          <div class="time">
            {{item.create_time}}
          </div>
        </div>
      </div>
    </div>

    <!-- 商品描述 -->
    <div class="desc" v-html="shopDatas.content">
    </div>

    <!-- 底部 -->
    <div class="footer">
      <div @click="$router.push('/')" class="icon-home">
        <van-icon name="wap-home-o" />
        <span>首页</span>
      </div>
      <div @click="$router.push('/cart')" class="icon-cart">
        <van-icon name="shopping-cart-o" to="/cart" :badge="cartTotal"/>
        <span>购物车</span>
      </div>
      <!-- 弹窗操作 -->
      <div class="btn-add" @click="addCartFn" >加入购物车</div>
      <div class="btn-buy" @click="buyFn" >立刻购买</div>
    </div>

    <!-- 购物车与购买弹层 -->
    <van-action-sheet v-model="show" :title="mode">
    <div class="product">
      <div class="product-title">
        <div class="left">
          <img :src="shopDatas.goods_image" alt="">
        </div>
        <div class="right">
          <div class="price">
            <span>¥</span>
            <span class="nowprice">{{shopDatas.goods_price_min}}</span>
          </div>
          <div class="count">
            <span>库存: </span>
            <span>{{ shopDatas.stock_total }}</span>
          </div>
        </div>
      </div>
      <div class="num-box">
        <span>数量</span>
        <!-- <CountBox :value="shopDatas.stock_total" @input="shopDatas.stock_total = $event"></CountBox> -->
        <CountBox v-model="addCount"></CountBox>
      </div>
      <div class="showbtn" v-if="shopDatas.stock_total > 0">
        <div class="btn" v-if="mode==='加入购物车'" @click="addCarts">加入购物车</div>
        <div class="btn now" v-else @click="goPay">立刻购买</div>
      </div>
      <div class="btn-none" v-else>该商品已抢完</div>
    </div>
  </van-action-sheet>
  </div>
</template>

<script>
import { getDetail } from '@/api/detail'
import { getComments } from '@/api/comment'
import { addCartApi, getCartList } from '@/api/cart' // 添加到购物车接口
import defaultImg from '@/assets/default-avatar.png'// 设置默认头像
import CountBox from '@/components/CountBox'
import { Toast } from 'vant'
import { mapGetters } from 'vuex'
import loginConfirms from '@/mixins/loginConfirm'

export default {
  name: 'ProDetail',
  mixins: [loginConfirms],
  components: {
    CountBox
  },
  data () {
    return {
      shopDatas: {}, // 商品详情数据
      images: [], // 商品轮播图数据
      current: 0,
      commentsList: [], // 评论数据
      commentsTotal: '', // 评论数量
      defaultImg,
      show: false,
      mode: '',
      addCount: 1,
      cartTotal: 0
    }
  },
  // 渲染页面
  created () {
    this.getDetails()
    this.getCommentss()

    // 页面刷新调用购物车数量
    this.getCartNums()
    // }
  },
  computed: {
    ...mapGetters('cart', ['shopTotalNums'])
  },
  methods: {
    onChange (index) {
      this.current = index
    },

    // 封装获取商品详情
    async getDetails () {
      const res = await getDetail(this.$route.params.id)

      const { data: { detail } } = res
      // 将其他信息存为对象
      this.images = detail.goods_images
      // 商品详情轮播图图片数组
      this.shopDatas = detail
    },

    // 封装获取商品评论
    async getCommentss () {
      const res = await getComments(this.$route.params.id)
      const { data: { list, total } } = res
      this.commentsList = list
      this.commentsTotal = total
    },
    // 页面刷新调用购物车数量
    async getCartNums () {
      // 没有登录就不获取购物车数量
      if (!this.$store.state.user.userinfo.token) return
      const { data } = await getCartList()
      this.cartTotal = data.cartTotal
    },

    // 添加到购物车
    async addCarts () {
      // 判断是否有token，没有弹窗去跳转
      if (this.loginConfirms()) return
      // 调用添加购物车的接口
      const { data } = await addCartApi(this.$route.params.id, this.addCount, this.shopDatas.skuList[0].goods_sku_id)
      this.show = false
      this.cartTotal = data.cartTotal
      Toast.success('添加购物车成功')
    },

    addCartFn () {
      this.show = true
      this.mode = '加入购物车'
    },
    buyFn () {
      this.show = true
      this.mode = '立即购买'
    },
    // 购买跳转订单页面
    goPay () {
      // 判断是否有token，没有弹窗去跳转
      if (this.loginConfirms()) return
      this.$router.push({
        path: '/pay',
        query: {
          mode: 'buyNow',
          goodsId: this.$route.params.id,
          goodsNum: this.addCount,
          goodsSkuId: this.shopDatas.skuList[0].goods_sku_id
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.prodetail {
  padding-top: 46px;

  ::v-deep .van-icon-arrow-left {
    color: #333;
  }

  img {
    display: block;
    width: 100%;
  }

  .custom-indicator {
    position: absolute;
    right: 10px;
    bottom: 10px;
    padding: 5px 10px;
    font-size: 12px;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 15px;
  }

  .desc {
    width: 100%;
    overflow: scroll;

    ::v-deep img {
      display: block;
      width: 100% !important;
    }
  }

  .info {
    padding: 10px;
  }

  .title {
    display: flex;
    justify-content: space-between;

    .now {
      color: #fa2209;
      font-size: 20px;
    }

    .oldprice {
      color: #959595;
      font-size: 16px;
      text-decoration: line-through;
      margin-left: 5px;
    }

    .sellcount {
      color: #959595;
      font-size: 16px;
      position: relative;
      top: 4px;
    }
  }

  .msg {
    font-size: 16px;
    line-height: 24px;
    margin-top: 5px;
  }

  .service {
    display: flex;
    justify-content: space-between;
    line-height: 40px;
    margin-top: 10px;
    font-size: 16px;
    background-color: #fafafa;

    .left-words {
      span {
        margin-right: 10px;
      }

      .van-icon {
        margin-right: 4px;
        color: #fa2209;
      }
    }
  }

  .comment {
    padding: 10px;
  }

  .comment-title {
    display: flex;
    justify-content: space-between;

    .right {
      color: #959595;
    }
  }

  .comment-item {
    font-size: 16px;
    line-height: 30px;

    .top {
      height: 30px;
      display: flex;
      align-items: center;
      margin-top: 20px;

      img {
        width: 20px;
        height: 20px;
      }

      .name {
        margin: 0 10px;
      }
    }

    .time {
      color: #999;
    }
  }

  .footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 55px;
    background-color: #fff;
    border-top: 1px solid #ccc;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    .icon-home,
    .icon-cart {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      font-size: 14px;

      .van-icon {
        font-size: 24px;
      }
    }

    .btn-add,
    .btn-buy {
      height: 36px;
      line-height: 36px;
      width: 120px;
      border-radius: 18px;
      background-color: #ffa900;
      text-align: center;
      color: #fff;
      font-size: 14px;
    }

    .btn-buy {
      background-color: #fe5630;
    }
  }
}

.tips {
  padding: 10px;
}

.product {
  .product-title {
    display: flex;
    .left {
      img {
        width: 90px;
        height: 90px;
      }
      margin: 10px;
    }
    .right {
      flex: 1;
      padding: 10px;
      .price {
        font-size: 14px;
        color: #fe560a;
        .nowprice {
          font-size: 24px;
          margin: 0 5px;
        }
      }
    }
  }

  .num-box {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    align-items: center;
  }

  .btn, .btn-none {
    height: 40px;
    line-height: 40px;
    margin: 20px;
    border-radius: 20px;
    text-align: center;
    color: rgb(255, 255, 255);
    background-color: rgb(255, 148, 2);
  }
  .btn.now {
    background-color: #fe5630;
  }
  .btn-none {
    background-color: #cccccc;
  }
}

</style>
