import request from '@/utils/request'

// 获取商品评论用在详情页
export const getComments = (goodsId) => {
  return request.get('/comment/listRows', {
    params: {
      goodsId,
      limit: 3 // 评论数量
    }
  })
}

// 获取商品评价总数
export const getCommentsTotal = (goodsId) => {
  return request.get('/comment/total', {
    params: {
      goodsId
    }
  })
}
// 获取商品评论（好，中，差）
export const getCommentsList = (scoreType, goodsId, page) => {
  return request.get('/comment/list', {
    params: {
      scoreType,
      goodsId,
      page
    }
  })
}
