export default {
  methods: {
    // 封装一个方法判断有无弹窗
    loginConfirms () {
      if (!this.$store.state.user.userinfo.token) {
        this.$dialog.confirm({
          title: '温馨提示',
          message: '您需要登录才能进行此操作哦',
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            // 用query传递过去的当前路径，
            this.$router.replace({
              path: '/login',
              query: {
                backUrl: this.$route.fullPath
              }
            })
          })
          .catch(() => { })
        return true // 如果弹窗了，返回true
      }
      return false // 没弹窗返回false
    }
  }

}
